@import url(https://fonts.googleapis.com/css?family=Raleway:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* background-color: #0f0e17 !important; */
  /* color: #fffffe !important; */
}



@media (max-width: 720px) {
  body {
    text-align: center !important;
  }
}

body{font-family:'Raleway', sans-serif !important;padding:1em}p{max-width:90%;text-align:justify;padding-left:1em;padding-right:1em}.title{color:#f25042 !important}.section-title{color:#f25042 !important;text-decoration:underline;margin-bottom:1em;font-weight:bold}.about-photo{border:2px solid #f25042 !important;border-radius:50%}.navigation{display:flex;flex-direction:row;align-items:initial;justify-content:center;position:absolute;top:50vh}.skills{margin-bottom:5em;margin-top:5em}.skills .skills-container{margin-top:2em;display:grid;grid-template-columns:repeat(3, 1fr)}.skills .skills-container i{margin-bottom:0.5em;font-size:8em}.skills .skills-container i:hover{color:#f25042 !important}.work{margin-top:4em;border-radius:10px}.work .company{margin-right:0.5em;font-weight:bold}.work .dates{font-style:italic}.work .work-description{margin-top:1em;margin-bottom:2em;text-align:justify}.work .work-company-date-container{margin-bottom:2em}.portfolio{margin-top:80px;display:flex;flex-direction:column;justify-content:center}.portfolio .projects{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));grid-gap:10px}.portfolio .projects .card{margin:10px}.portfolio .projects .card .card-title{text-align:center}.portfolio .projects .card .card-text{text-align:left}.portfolio .projects .card .links{display:flex;justify-content:center}.portfolio a{color:#f25042}.portfolio .project-link{margin-left:1em}.portfolio .github-link{margin-right:1em}.portfolio i{padding-right:0.5em}.portfolio .card-text{max-width:100%}@media (max-width: 720px){.work .company{display:block}.skills .skills-container{grid-template-columns:repeat(3, 1fr);padding:0}.skills .skills-container i{font-size:5em}.portfolio{align-items:center}}@media (max-width: 1024px){p{max-width:100%}}


